.mobile-nav {
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  position: fixed;
  left: 0;
  top: 60px;
  background-color: #ffffff;
  z-index: 10000;
  transition: all .5s ease;
  transform: translateX(-120%); }
  .mobile-nav.active {
    transform: translateX(0); }
  .mobile-nav .mobile-nav__content {
    display: flex;
    flex-direction: column;
    padding-top: 40px; }
  .mobile-nav .user {
    display: flex;
    align-items: center;
    margin-bottom: 30px; }
    .mobile-nav .user .name {
      font-size: 14px;
      padding-left: 20px;
      display: block;
      font-weight: 600; }
  .mobile-nav .nav-top {
    display: block; }
    .mobile-nav .nav-top ul {
      width: auto;
      flex-direction: column; }
      .mobile-nav .nav-top ul li {
        margin-bottom: 15px; }
        .mobile-nav .nav-top ul li a {
          display: inline-block; }
